.navbar {
    background-color: rgb(34,35,40);
    height: 90px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 1.2rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 20;
    font-family: 'Open Sans', sans-serif;
  }
  
  .nav-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height: 80px;
    max-width: 1500px;
  }
  
  .nav-logo {
    color: rgb(247,87,87);
    -webkit-align-items: center;
            align-items: center;
    margin-left: 115px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    margin-top: -10px;
  }
  
  .nav-menu {
    display: -webkit-flex;
    display: flex;
    list-style: none;
    text-align: center;
    margin-right: 7.5rem;
    margin-top: 10px;
  }
  
  .nav-links {
    color: #fff;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    border-bottom: 3px solid transparent;
  }
  .fa-code {
    margin-left: 1rem;
  }
  
  .nav-item {
    line-height: 40px;
    margin-right: 1rem;
  }
  
  .nav-item:hover:after {
    width: 100%;
    background: rgb(247,87,87);
  }
  
  .nav-item .active {
    color: rgb(247,87,87);
  }
  
  .nav-icon {
    margin-top: -2px;
    display: none;
  }
  .s{
    border: 2px solid rgb(247,87,87);
    border-radius: 25px;
    padding-left: 31px;
    padding-right: 31px;
    padding-bottom: 10px;
    padding-top: 10px;
    margin-left: 55px;
    margin-top: -14px;
    cursor: pointer;
    color: white;
    
  }
  .s:hover{
    background-color: rgb(247,87,87);
    color: white;
  }
  .nav-links span{
color: white;
  }
  .nav-links:hover{
    color: red;
  }
  @media screen and (max-width: 1100px) {
    .nav-logo{
      margin-left: 30px;
    }
    .nav-menu{
      margin-right: 2rem;
    }
    
  }
  
  @media screen and (max-width: 960px) {
    .nav-menu {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      width: 100%;
      border-top: 1pxsolid #fff;
      position: absolute;
      top: 80px;
      left: -110%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: rgb(34,35,40);
      left: 0px;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
    .nav-item .active {
      color: rgb(247,87,87);
      border: none;
    }
    .nav-links {
      padding: 1.5rem;
      width: 100%;
      display: table;
    }
  
    .nav-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      -webkit-transform: translate(-100%, 60%);
              transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
      color: rgb(247,87,87);
    }
    .s{
      width: 150px;
      height: 60px;
      display: inline-block;
      margin: 14px 60px;
      line-height: 8px;
      border: 2px  solid red;
      border-radius: 300px;
    }
    
  }
